* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.search-form {
  position: fixed;
  width: 100%;
  top: 0%;
  display: flex;
  justify-content: center;
  padding: 10px;
  background-color: rgb(100, 100, 255);

  border-radius: 3px;
}
.search-form__input {
  border-top: 1px solid antiquewhite;
  border-left: 1px solid antiquewhite;
  border-bottom: 1px solid antiquewhite;
  border-right: none;

  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  padding: 5px 1%;
}

.search-form__btn {
  display: flex;
  padding: 2px 10px;
  font-size: larger;
  line-height: 1.2;
  letter-spacing: 1px;

  border-top: 1px solid antiquewhite;
  border-right: 1px solid antiquewhite;
  border-bottom: 1px solid antiquewhite;
  border-left: none;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;

  background-color: white;
  color: black;
}

.search-form__icon {
  fill: rgb(100, 100, 255);
}

.search-form__btn:active {
  background-color: rgb(100, 100, 255);
  color: white;
}

.search-form__auto-scroll {
  padding: 0px 15px;
  display: block;
  border: 1px solid antiquewhite;
  background-color: white;
  color: rgb(100, 100, 255);
}
.search-form__auto-scroll:active {
  background-color: rgb(100, 100, 255);
}

.gallery {
  margin-top: 40px;
  margin-left: -20px;
  margin-bottom: -20px;
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: stretch;
}

.photo-card {
  width: 270px;
  margin-left: 20px;
  margin-bottom: 20px;
  border: 1px solid black;
  box-shadow: 10px 10px 8px 2px rgba(0, 0, 0, 0.3);
}

.photo-card img {
  display: block;
  width: 100%;

  height: 200px;

  border: 1px solid black;
}

.info {
  display: flex;
  justify-content: center;
  align-items: center;

  font-size: 14px;
  padding: 10px;

  border: 1px solid black;
}

.info-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.info-item:not(:last-child) {
  margin-right: 15px;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.next-btn {
  display: block;
  margin: 20px auto 40px;
  padding: 5px 15px;

  color: rgb(100, 100, 255);
  border: 1px solid rgb(100, 100, 255);
  border-radius: 5px;
  background-color: white;
}

.next-btn:active {
  background-color: rgb(255, 159, 159);
}

.end {
  text-align: center;
  color: rgb(100, 100, 255);
}
